import React, { useState } from 'react';
import { Participant, RaceResult } from './App';
import RaceTrack from './RaceTrack';

interface GameProps {
  participants: Participant[];
  onRaceEnd: (results: RaceResult[]) => void;
}

const Game: React.FC<GameProps> = ({ participants, onRaceEnd }) => {
  const [isRacing, setIsRacing] = useState(true);

  const handleRaceComplete = (results: RaceResult[]) => {
    console.log('Race completed in Game component:', results); // ë””ë²„ê¹…ìš© ë¡œê·¸
    setIsRacing(false);
    // ê²°ê³¼ë¥¼ ì¦‰ì‹œ ì „ë‹¬í•˜ë˜, í™”ë©´ ì „í™˜ì„ ìœ„í•œ ì•½ê°„ì˜ ì§€ì—° ì¶”ê°€
    setTimeout(() => {
      onRaceEnd(results);
    }, 500);
  };

  return (
    <div className="game">
      <RaceTrack
        participants={participants}
        raceInProgress={isRacing}
        onRaceComplete={handleRaceComplete}
      />
    </div>
  );
};

export default Game;
